import Content from 'components/ui/Content';
import Typography from 'components/ui/Typography';
import Chip from 'components/ui/Chip';
import Divider from 'components/ui/Divider';
import Card from 'components/ui/Card';
import Button from 'components/ui/Button';
import Tooltip from 'components/ui/Tooltip';

import classNames from 'classnames';

import EfficientFrontierImage from './images/icon-efficient-frontier_4x.webp';
import CertCompleteImage from './images/icon-cert-compleate_4x.webp';

import type { TDiagnosisResultAdvisor } from '../types';

type ChipColor = 'red' | 'green' | 'yellow' | 'blue';

interface DiagnosisResultAdvisorProps {
  data: TDiagnosisResultAdvisor[];
  responseId: string;
  onFrontier?: () => void;
  onFeeAnalize?: () => void;
}

const BACKGROUD_COLOR_MAPS = {
  'Active Accumulator': 'bg-red-default',
  'Independent Individualist': 'bg-yellow-default',
  'Friendly Follower': 'bg-green-default',
  'Passive Preserver': 'bg-primary-500'
};

const COLOR_MAPS = {
  'Active Accumulator': 'text-red-default/40',
  'Independent Individualist': 'text-yellow-default/40',
  'Friendly Follower': 'text-green-default/40',
  'Passive Preserver': 'text-primary-500/40'
};

const CONST_CHIP_MAPS = {
  'Active Accumulator': 'red',
  'Independent Individualist': 'yellow',
  'Friendly Follower': 'green',
  'Passive Preserver': 'blue'
};

const DiagnosisResultAdvisor = ({
  data,
  responseId,
  onFrontier,
  onFeeAnalize
}: DiagnosisResultAdvisorProps) => {
  const result = data.find((item) => item.id === Number(responseId));

  if (!result) {
    return <div>Error: Data not found</div>;
  }

  const { type, typeEng, keyPoints, strategy, solution } = result;

  return (
    <div className="flex flex-col items-center mx-auto">
      <div
        className={classNames(
          'flex items-center gap-x-3 mt-3 py-2 px-5 rounded-card-xl',
          BACKGROUD_COLOR_MAPS[typeEng]
        )}
      >
        <Typography size="lg" weight={700} color="text-white">
          {type}
        </Typography>
        <i className="inline-flex w-px h-4 bg-white/30" />
        <Typography color="text-white/80">{typeEng}</Typography>
      </div>

      <Divider spacing={32} color="border-gray-300" />

      <div className="flex flex-col items-center space-y-5">
        <Typography size="xs" weight={700} color="text-gray-700">
          {type}의 편향
        </Typography>

        <div className="flex flex-wrap gap-3 pb-5">
          {keyPoints?.map(({ label, tooltipTitle, tooltipContent }) => {
            return tooltipTitle ? (
              <Tooltip
                key={label}
                id={`tooltip-${label}`}
                content={
                  <>
                    <Tooltip.Title>{tooltipTitle}</Tooltip.Title>
                    <Tooltip.Body>{tooltipContent}</Tooltip.Body>
                  </>
                }
              >
                <Chip
                  label={label}
                  prefix={<Typography color={COLOR_MAPS[typeEng]}>#</Typography>}
                  color={CONST_CHIP_MAPS[typeEng] as ChipColor}
                />
              </Tooltip>
            ) : (
              <Chip
                key={label}
                label={label}
                prefix={<Typography color={COLOR_MAPS[typeEng]}>#</Typography>}
                color={CONST_CHIP_MAPS[typeEng] as ChipColor}
              />
            );
          })}
        </div>
      </div>

      <Content gap={40} pt="pt-10" px="px-0">
        <Content.Section gap={20}>
          <Content.SectionTitle>운용 전략</Content.SectionTitle>
          <div className="flex flex-col gap-y-2">
            {strategy.map((_item, index) => (
              <Typography key={index} size="lg" color="text-gray-700">
                {_item}
              </Typography>
            ))}
          </div>
        </Content.Section>

        <Content.Section gap={20}>
          <Content.SectionTitle>솔루션 방안</Content.SectionTitle>
          {solution.map((_item, index) => (
            <Typography key={index} size="lg" color="text-gray-700">
              {_item}
            </Typography>
          ))}
        </Content.Section>

        <Content.Section gap={20}>
          <Content.SectionTitle>쿼터백 제안 도구</Content.SectionTitle>
          <Card py="py-8" px="px-8">
            <div className="flex items-center w-full">
              <div className="flex flex-col items-center justify-center gap-y-2 flex-1 w-full">
                <div className="flex items-center justify-center flex-1 w-full py-2 px-[3.25rem]">
                  <i className="inline-flex items-center justify-center w-[4.25rem] h-[4.25rem] bg-primary-50 rounded-button-full">
                    <img src={EfficientFrontierImage} width={40} height={40} />
                  </i>
                </div>
                <Button variant="outline" character="inline" onClick={onFrontier}>
                  Efficient Frontier
                </Button>
              </div>

              <Divider isHorizontal={false} spacing={28} color="border-gray-200" />

              <div className="flex flex-col items-center justify-center gap-y-2 flex-1 w-full">
                <div className="flex items-center justify-center flex-1 w-full py-2 px-[3.25rem]">
                  <i className="inline-flex items-center justify-center w-[4.25rem] h-[4.25rem] bg-primary-50 rounded-button-full">
                    <img src={CertCompleteImage} width={40} height={40} />
                  </i>
                </div>
                <Button variant="outline" character="inline" onClick={onFeeAnalize}>
                  Fee Analize
                </Button>
              </div>
            </div>
          </Card>
        </Content.Section>
      </Content>
    </div>
  );
};

export default DiagnosisResultAdvisor;
